import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./topSalesProductService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllProduct } from "../product/productService";

class TopSalesProductForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["TOP_SALES_PRODUCT_CREATE","TOP_SALES_PRODUCT_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/topSalesProducts/",
	userPermissions: [],
    data: { id: null , name: "", productId: "", topSalesProductImage: "", bannerText: "" },
    errors: {},
    validationMessage: {
      name: '',
topSalesProductImage: '',
bannerText: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Top Sales Product",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	nameLabel: "Name",
namePlaceholder: "Enter Name",
productIdLabel: "Product",
productIdPlaceholder: "Select Product",
topSalesProductImageLabel: "Top Sales Product Image",
topSalesProductImagePlaceholder: "Enter Top Sales Product Image",
bannerTextLabel: "Banner Title",
bannerTextPlaceholder: "Enter Banner Title",

    },
	  productOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    name: Joi.string().required().label({this.state.translations.nameLabel}),product: Joi.number().allow(null),topSalesProductImage: Joi.string().required().label({this.state.translations.topSalesProductImageLabel}),bannerText: Joi.string().required().label({this.state.translations.bannerTextLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      await getAllProduct().then(function (result) {
        let productOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          productOptions.push({
            value: result.data[i].id,
            label: result.data[i].title,
          });
        }
        thisContext.setState({ productOptions: productOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.topSalesProduct.titleLabel"] ? translationsObj["i18n.topSalesProduct.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          nameLabel: translationsObj["i18n.topSalesProduct.name.label"] ? translationsObj["i18n.topSalesProduct.name.label"] : thisContext.state.translations.nameLabel,
namePlaceholder: translationsObj["i18n.topSalesProduct.name.placeholder"] ? translationsObj["i18n.topSalesProduct.name.placeholder"] : thisContext.state.translations.namePlaceholder,
productIdLabel: translationsObj["i18n.topSalesProduct.productId.label"] ? translationsObj["i18n.topSalesProduct.productId.label"] : thisContext.state.translations.productIdLabel,
productIdPlaceholder: translationsObj["i18n.topSalesProduct.productId.placeholder"] ? translationsObj["i18n.topSalesProduct.productId.placeholder"] : thisContext.state.translations.productIdPlaceholder,
topSalesProductImageLabel: translationsObj["i18n.topSalesProduct.topSalesProductImage.label"] ? translationsObj["i18n.topSalesProduct.topSalesProductImage.label"] : thisContext.state.translations.topSalesProductImageLabel,
topSalesProductImagePlaceholder: translationsObj["i18n.topSalesProduct.topSalesProductImage.placeholder"] ? translationsObj["i18n.topSalesProduct.topSalesProductImage.placeholder"] : thisContext.state.translations.topSalesProductImagePlaceholder,
bannerTextLabel: translationsObj["i18n.topSalesProduct.bannerText.label"] ? translationsObj["i18n.topSalesProduct.bannerText.label"] : thisContext.state.translations.bannerTextLabel,
bannerTextPlaceholder: translationsObj["i18n.topSalesProduct.bannerText.placeholder"] ? translationsObj["i18n.topSalesProduct.bannerText.placeholder"] : thisContext.state.translations.bannerTextPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("TOP_SALES_PRODUCT_CREATE") ||
      thisContext.state.userPermissions.includes("TOP_SALES_PRODUCT_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      name: obj.name,productId: obj.productId,topSalesProductImage: obj.topSalesProductImage,bannerText: obj.bannerText,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-light">
                <Row>
                  <Col md='1'>
                    {
                      this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                        e.preventDefault();
                        thisContext.props.history.push(thisContext.state.basicUrl);
                      })
                    }
                  </Col>
                  <Col md='11'>
                    <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                  </Col>
                </Row>
              </Card.Title>
			{/*<Card.Header>
				<p>Hello Top Sales Product please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}

                {this.renderInput(false,false,"id", "Id", "hidden")}
                <Row><Col>{this.renderInput(true,true,"name", this.state.translations.nameLabel,this.state.translations.namePlaceholder  ,"text",false,this.state.validationMessage.name)}</Col></Row>
<Row><Col>{this.renderSelect(false,false,"productId", this.state.translations.productIdLabel, this.state.translations.productIdPlaceholder, thisContext.state.productOptions, this.state.validationMessage.productId)}</Col></Row>
<Row><Col>{this.renderImageInput(true,"topSalesProductImage",this.state.translations.topSalesProductImageLabel, this.state.translations.topSalesProductImagePlaceholder, this.state.validationMessage.topSalesProductImage)}</Col></Row><Row><Col>{this.renderInput(true,true,"bannerText", this.state.translations.bannerTextLabel,this.state.translations.bannerTextPlaceholder  ,"textarea",false,this.state.validationMessage.bannerText)}</Col></Row>

                {/* {this.renderInput("dueDate", "Due Date")} */}

                
            </Card.Body>

				{thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
          		</Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default TopSalesProductForm;
