import React from "react";
import _ from "lodash";
import PropTypes from "prop-types";
import { Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";

const Pagination = (props) => {
  const {
    currentPage,
    itemsCount,
    pageSize,
    onPageChange,
    firstPage,
    lastPage,
  } = props;

  const pagesCount = Math.ceil(itemsCount / pageSize);
  
  if (pagesCount === 1) return null; // No pagination needed

  const maxPagesToShow = 10; // Adjust as needed
  const pages = [];

  // Always show the first 3 pages
  for (let i = 1; i <= Math.min(3, pagesCount); i++) {
    pages.push(i);
  }

  // Add ellipsis if there are more pages to the right
  if (currentPage > 4 && pagesCount > 6) {
    pages.push("...");
  }

  // Add current and surrounding pages
  const startPage = Math.max(currentPage - 1, 4);
  const endPage = Math.min(currentPage + 1, pagesCount - 3);
  
  for (let i = startPage; i <= endPage; i++) {
    if (i > 3 && i < pagesCount - 2) {
      pages.push(i);
    }
  }

  // Add ellipsis if there are more pages to the left
  if (currentPage < pagesCount - 3 && pagesCount > 6) {
    pages.push("...");
  }

  // Always show the last 3 pages
  for (let i = Math.max(pagesCount - 2, 4); i <= pagesCount; i++) {
    if (!pages.includes(i)) {
      pages.push(i);
    }
  }

  return (
    <React.Fragment>
      <nav aria-label="Page navigation example">
        <ul className="pagination">
          <li>
            <Button
              disabled={currentPage === 0}
              className="page-link"
              onClick={() => onPageChange(currentPage - 1)}
            >
              <FontAwesomeIcon icon={faArrowLeft} />
            </Button>
          </li>
          
          {pages.map((page, index) => (
            <li key={index} className={page === (currentPage+1) ? "page-item active" : "page-item"}>
              {page === "..." ? (
                <span className="page-link">{page}</span>
              ) : (
                <Button className="page-link" onClick={() => onPageChange(page-1)}>
                  {page}
                </Button>
              )}
            </li>
          ))}

          <li>
            <Button
              disabled={currentPage === (pagesCount-1)}
              className="page-link"
              onClick={() => onPageChange(currentPage + 1)}
            >
              <FontAwesomeIcon icon={faArrowRight} />
            </Button>
          </li>
        </ul>
      </nav>
    </React.Fragment>
  );
};

Pagination.propTypes = {
  itemsCount: PropTypes.number.isRequired,
  pageSize: PropTypes.number.isRequired,
  currentPage: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};

export default Pagination;
