import http from "../../common/httpService";
import config from "../../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/appPermissions";

function appPermissionUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5, fromDate, toDate, status, searchToken) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size 
	+ "&fromDate=" + fromDate + "&toDate=" + toDate + "&status=" + status + "&searchToken=" + searchToken, headerOptions);
}

export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(appPermissionId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(appPermissionUrl(appPermissionId), options);
}

export function save(datObj) {
	return datObj.id !== null ? update(datObj) : create(datObj);
}

export function create(appPermission) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, appPermission, options);
}

export function update(appPermission) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, appPermission, options);
}

export function remove(appPermissionId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(appPermissionUrl(appPermissionId), options);
}

