import React from "react";
import { Col, Button, Row } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

const DataFilterControls = ({
  fromDate,
  toDate,
  searchToken,
  categories,
  currentItem,
  onCategoryChange,
  onSearchTokenChange,
  onFromDateChange,
  onToDateChange,
  onSearch,
}) => {
  return (
    <Row className="pb-3">
      <Col sm='12' md='6' xs='12' lg='3'>

        <div class="input-group mb-1">
          <span class="input-group-text">From</span>
          <input
            className="form-control"
            name="fromDate"
            type="date"
            value={fromDate}
            onChange={onFromDateChange}
          />
        </div>
      </Col>
      <Col sm='12' md='6' xs='12' lg='3'>
        <div class="input-group mb-1">
          <span class="input-group-text">To</span>
          <input
            className="form-control"
            name="toDate"
            type="date"
            value={toDate}
            onChange={onToDateChange}
          />
        </div>
      </Col>
      <Col sm='12' md='6' xs='12' lg='3'>
        <div class="input-group mb-1">
          <span class="input-group-text">Status</span>
          <select class="form-select form-control w-auto" id="statusFilter"
            value={currentItem}
            onChange={(e) => onCategoryChange(e.target.value)}
          >
            <option value={""}>-Select Status-</option>
            {categories.map((category) => (
              <option value={category} key={category}>
                {category}
              </option>
            ))}
          </select>
        </div>


      </Col>
      <Col sm='12' md='6' xs='12' lg='3'>

        <div class="input-group mb-1">
          <input
            type="text"
            onChange={onSearchTokenChange}
            value={searchToken}
            className="form-control"
            placeholder="Search..."
            aria-label="Search Token"
          />
          <div className="input-group-append">
            <div className="input-group-text form-control">
              <Button onClick={onSearch} variant="none" size="sm" className="mr-2">
                <FontAwesomeIcon icon={faSearch} />
              </Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  );
};

export default DataFilterControls;
