import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./customerOrderService";
// import Joi from "joi-browser";
import UxSelect from "../common/form_attributes/UxSelect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllCustomer } from "../customer/customerService";
import { getAll as getAllProduct } from "../product/productService"; import { getAll as getAllPaymentMethod } from "../paymentMethod/paymentMethodService";

class CustomerOrderForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["CUSTOMER_ORDER_CREATE", "CUSTOMER_ORDER_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/customerOrders/",
    userPermissions: [],
    data: { id: null, customerOrderNumber: "", customerId: "", orderDate: "", deliveryDate: "", customerOrderItems: [{ productId: "", quantity: "", unitPrice: "", totalPrice: "", }], totalAmount: "", paymentMethodId: "", remarks: "" },
    errors: {},
    validationMessage: {
      customer: '',
      orderDate: '',
      deliveryDate: '',
      totalAmount: '',
      remarks: '',

    },
    commonValidationMessage: "",
    translations: {
      titleLabel: "Customer Order",
      homeLabel: "Home",
      cancelLabel: "Cancel",
      saveLabel: "Save",
      // orgNameLabel: "Organization Name",
      customerOrderNumberLabel: "Customer Order Number",
      customerOrderNumberPlaceholder: "Enter Customer Order Number",
      customerIdLabel: "Customer",
      customerIdPlaceholder: "Select Customer",
      orderDateLabel: "Order Date",
      orderDatePlaceholder: "Enter Order Date",
      deliveryDateLabel: "Delivery Date",
      deliveryDatePlaceholder: "Enter Delivery Date",
      customerOrderItemLabel: "Customer Order Items",
      customerOrderItemPlaceholder: "Create Customer Order Items",
      totalAmountLabel: "Total Amount",
      totalAmountPlaceholder: "Enter Total Amount",
      paymentMethodIdLabel: "Payment Method",
      paymentMethodIdPlaceholder: "Select Payment Method",
      remarksLabel: "Remarks",
      remarksPlaceholder: "Enter Remarks",

    },
    customerOptions: [], productOptions: [], customerOrderItemOptions: [], paymentMethodOptions: [],
  };


  /* schema = {
      id: Joi.number().allow(null),
      customerOrderNumber: Joi.string().label({this.state.translations.customerOrderNumberLabel}),customer: Joi.number().label("Customer"),orderDate: Joi.string().required().label({this.state.translations.orderDateLabel}),deliveryDate: Joi.string().required().label({this.state.translations.deliveryDateLabel}),totalAmount: Joi.string().required().label({this.state.translations.totalAmountLabel}),paymentMethod: Joi.number().allow(null),remarks: Joi.string().required().label({this.state.translations.remarksLabel}),
    };
  */
  async populateData() {
    try {
      const thisContext = this;
      await getAllCustomer().then(function (result) {
        let customerOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          customerOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ customerOptions: customerOptions });
      });
      await getAllProduct().then(function (result) {
        let productOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          productOptions.push({
            value: result.data[i].id,
            label: result.data[i].title,
          });
        }
        thisContext.setState({ productOptions: productOptions });
      });
      await getAllPaymentMethod().then(function (result) {
        let paymentMethodOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          paymentMethodOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ paymentMethodOptions: paymentMethodOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length - 1];
      if (id === undefined || id === 'new') {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
          saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
          homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
          titleLabel: translationsObj["i18n.customerOrder.titleLabel"] ? translationsObj["i18n.customerOrder.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          customerOrderNumberLabel: translationsObj["i18n.customerOrder.customerOrderNumber.label"] ? translationsObj["i18n.customerOrder.customerOrderNumber.label"] : thisContext.state.translations.customerOrderNumberLabel,
          customerOrderNumberPlaceholder: translationsObj["i18n.customerOrder.customerOrderNumber.placeholder"] ? translationsObj["i18n.customerOrder.customerOrderNumber.placeholder"] : thisContext.state.translations.customerOrderNumberPlaceholder,
          customerIdLabel: translationsObj["i18n.customerOrder.customerId.label"] ? translationsObj["i18n.customerOrder.customerId.label"] : thisContext.state.translations.customerIdLabel,
          customerIdPlaceholder: translationsObj["i18n.customerOrder.customerId.placeholder"] ? translationsObj["i18n.customerOrder.customerId.placeholder"] : thisContext.state.translations.customerIdPlaceholder,
          orderDateLabel: translationsObj["i18n.customerOrder.orderDate.label"] ? translationsObj["i18n.customerOrder.orderDate.label"] : thisContext.state.translations.orderDateLabel,
          orderDatePlaceholder: translationsObj["i18n.customerOrder.orderDate.placeholder"] ? translationsObj["i18n.customerOrder.orderDate.placeholder"] : thisContext.state.translations.orderDatePlaceholder,
          deliveryDateLabel: translationsObj["i18n.customerOrder.deliveryDate.label"] ? translationsObj["i18n.customerOrder.deliveryDate.label"] : thisContext.state.translations.deliveryDateLabel,
          deliveryDatePlaceholder: translationsObj["i18n.customerOrder.deliveryDate.placeholder"] ? translationsObj["i18n.customerOrder.deliveryDate.placeholder"] : thisContext.state.translations.deliveryDatePlaceholder,
          customerOrderItemLabel: translationsObj["i18n.customerOrder.customerOrderItem.label"] ? translationsObj["i18n.customerOrder.customerOrderItem.label"] : thisContext.state.translations.customerOrderItemLabel,
          customerOrderItemPlaceholder: translationsObj["i18n.customerOrder.customerOrderItem.placeholder"] ? translationsObj["i18n.customerOrder.customerOrderItem.placeholder"] : thisContext.state.translations.customerOrderItemPlaceholder,
          totalAmountLabel: translationsObj["i18n.customerOrder.totalAmount.label"] ? translationsObj["i18n.customerOrder.totalAmount.label"] : thisContext.state.translations.totalAmountLabel,
          totalAmountPlaceholder: translationsObj["i18n.customerOrder.totalAmount.placeholder"] ? translationsObj["i18n.customerOrder.totalAmount.placeholder"] : thisContext.state.translations.totalAmountPlaceholder,
          paymentMethodIdLabel: translationsObj["i18n.customerOrder.paymentMethodId.label"] ? translationsObj["i18n.customerOrder.paymentMethodId.label"] : thisContext.state.translations.paymentMethodIdLabel,
          paymentMethodIdPlaceholder: translationsObj["i18n.customerOrder.paymentMethodId.placeholder"] ? translationsObj["i18n.customerOrder.paymentMethodId.placeholder"] : thisContext.state.translations.paymentMethodIdPlaceholder,
          remarksLabel: translationsObj["i18n.customerOrder.remarks.label"] ? translationsObj["i18n.customerOrder.remarks.label"] : thisContext.state.translations.remarksLabel,
          remarksPlaceholder: translationsObj["i18n.customerOrder.remarks.placeholder"] ? translationsObj["i18n.customerOrder.remarks.placeholder"] : thisContext.state.translations.remarksPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("CUSTOMER_ORDER_CREATE") ||
        thisContext.state.userPermissions.includes("CUSTOMER_ORDER_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      customerOrderNumber: obj.customerOrderNumber, customerId: obj.customerId, orderDate: obj.orderDate, deliveryDate: obj.deliveryDate, customerOrderItems: obj.customerOrderItems, totalAmount: obj.totalAmount, paymentMethodId: obj.paymentMethodId, remarks: obj.remarks,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  addCustomerOrderItemRow = (e) => {
    e.preventDefault(); let updatedCustomerOrderItems = [...this.state.data.customerOrderItems, { productId: "", quantity: "", unitPrice: "", totalPrice: "", }];
    this.setState({
      data: {
        ...this.state.data, customerOrderItems: updatedCustomerOrderItems
      }
    });
    return;
  };

  handleCustomerOrderItemDelete = (e, index) => {
    e.preventDefault();
    const updatedCustomerOrderItems = [...this.state.data.customerOrderItems];
    updatedCustomerOrderItems.splice(index, 1);
    // this.state.data.customerOrderItems = updatedCustomerOrderItems;
    this.setState({
      data: {
        ...this.state.data, customerOrderItems: updatedCustomerOrderItems
      }
    });
    return;
  };

  handleCustomerOrderItemMultiRowChange = (index, e) => {
    const { name, value } = e.target;
    const updatedCustomerOrderItems = [...this.state.data.customerOrderItems];
    updatedCustomerOrderItems[index][name] = value;
    //this.state.data.customerOrderItems = updatedCustomerOrderItems;
    this.setState({
      customerOrderItems: updatedCustomerOrderItems
    });
    return;
  };
  handleCustomerOrderItemMultiRowSelectChange = (index, name, value) => {
    const updatedCustomerOrderItems = [...this.state.data.customerOrderItems];
    updatedCustomerOrderItems[index][name] = value;
    this.setState({
      productVariants: updatedCustomerOrderItems
    });
    return;
  };

  htmlContent() {
    let thisContext = this;
    const { data, errors } = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12" className="mt-2 mb-2">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-light">
                    <Row>
                      <Col md='1'>
                        {
                          this.renderCancelButton(thisContext.state.translations.cancelLabel, function (e) {
                            e.preventDefault();
                            thisContext.props.history.push(thisContext.state.basicUrl);
                          })
                        }
                      </Col>
                      <Col md='11'>
                        <h4 className="text-right pr-1">{this.state.translations.titleLabel}</h4>
                      </Col>
                    </Row>
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Customer Order please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}

                    {this.renderInput(false, false, "id", "Id", "hidden")}
                    <Row><Col>{this.renderInput(false, true, "customerOrderNumber", this.state.translations.customerOrderNumberLabel, this.state.translations.customerOrderNumberPlaceholder, "text", false, this.state.validationMessage.customerOrderNumber)}</Col></Row>
                    <Row><Col>{this.renderSelect(true, false, "customerId", this.state.translations.customerIdLabel, this.state.translations.customerIdPlaceholder, thisContext.state.customerOptions, this.state.validationMessage.customerId)}</Col></Row>
                    <Row><Col>{this.renderInput(true, true, "orderDate", this.state.translations.orderDateLabel, this.state.translations.orderDatePlaceholder, "date", false, this.state.validationMessage.orderDate)}</Col></Row>
                    <Row><Col>{this.renderInput(true, true, "deliveryDate", this.state.translations.deliveryDateLabel, this.state.translations.deliveryDatePlaceholder, "date", false, this.state.validationMessage.deliveryDate)}</Col></Row>
                    <table className="table table-striped">
                      <thead>
                        <tr>
                          <th>Product Id</th>
                          <th>Quantity</th>
                          <th>Unit Price</th>
                          <th>Total Price</th>
                          <th className="text-right"><button className="btn btn-sm btn-outline-success" onClick={this.addCustomerOrderItemRow}><FontAwesomeIcon icon={faPlus} /> Customer Order Item</button></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.data.customerOrderItems.map((customerOrderItem, index) => (
                          <tr key={index}>
                            <td><UxSelect
                              isRequired={false}
                              isMultiple={false}
                              name={'productId'}
                              label={""}
                              placeholder={"Please Select"}
                              options={thisContext.state.productOptions}
                              validationMessage={""}
                              value={customerOrderItem.productId}
                              updateSelected={(name, value) => this.handleCustomerOrderItemMultiRowSelectChange(index, name, value)}
                            /></td>
                            <td>
                              <input
                                type="number" class="form-control" placeholder="Enter Quantity"
                                name="quantity"
                                value={customerOrderItem.quantity}
                                onChange={(e) => this.handleCustomerOrderItemMultiRowChange(index, e)}
                              />
                            </td>
                            <td>
                              <input
                                type="number" class="form-control" placeholder="Enter Unit Price"
                                name="unitPrice"
                                value={customerOrderItem.unitPrice}
                                onChange={(e) => this.handleCustomerOrderItemMultiRowChange(index, e)}
                              />
                            </td>
                            <td>
                              <input
                                type="number" class="form-control" placeholder="Enter Total Price"
                                name="totalPrice"
                                value={customerOrderItem.totalPrice}
                                onChange={(e) => this.handleCustomerOrderItemMultiRowChange(index, e)}
                              />
                            </td>
                            <td className="text-right">
                              <button className="btn btn-sm btn-outline-danger" onClick={(e) => this.handleCustomerOrderItemDelete(e, index)}><FontAwesomeIcon icon={faTrash} /></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                    <Row><Col>{this.renderInput(true, true, "totalAmount", this.state.translations.totalAmountLabel, this.state.translations.totalAmountPlaceholder, "number", false, this.state.validationMessage.totalAmount)}</Col></Row>
                    <Row><Col>{this.renderSelect(true, false, "paymentMethodId", this.state.translations.paymentMethodIdLabel, this.state.translations.paymentMethodIdPlaceholder, thisContext.state.paymentMethodOptions, this.state.validationMessage.paymentMethodId)}</Col></Row>
                    <Row><Col>{this.renderInput(true, true, "remarks", this.state.translations.remarksLabel, this.state.translations.remarksPlaceholder, "textarea", false, this.state.validationMessage.remarks)}</Col></Row>

                    {/* {this.renderInput("dueDate", "Due Date")} */}


                  </Card.Body>

                  {thisContext.state.commonValidationMessage && <Card.Footer>
                    <Row>
                      <Col md="12">
                        <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
                      </Col>
                    </Row>
                  </Card.Footer>
                  }
                  <Card.Footer>
                    <Row>
                      <Col md='12' className="text-right">
                        {this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
                      </Col>
                    </Row>
                  </Card.Footer>
                </Card>
              </form>

            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default CustomerOrderForm;
