import React, { Component } from "react";
import Pagination from "../common/pagination";
// import { paginate } from "../common/paginate";
import { get, getAllPaginated, remove, save } from "./customerService";
import { checkAuthentication } from "../common/authenticationService";
// import ListGroup from "../common/listGroup";
import Container from "react-bootstrap/Container";
import { Col, Row, Card, Alert, Button } from "react-bootstrap";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { NavLink } from "react-router-dom";
// import customer from "./customer.json";
import CustomersTable from "./customerTable";
import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faSearch } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../common/UxCrudManager";
import DataFilterControls from "../common/DataFilterControls";

class CustomerManager extends UxCrudManager {
	state = {
		isLoading: true,
		errorDismissed: true,
		successDismissed: true,
		DATA_CREATE: "CUSTOMER_CREATE",
		requiredPermission: "CUSTOMER_READ",
		moduleUrl: "/admin",
		basicUrl: "/admin/customers/",
		catagorySelectionLabel: "All Customers",
		userPermissions: [],
		fromDate: "",
		toDate: "",
		searchToken: "",
		data: [],
		categories: [
			"DRAFT",
			"APPROVED",
			"REJECTED",
			"DELETED"
		],
		currentPage: 0,
		pageSize: 5,
		firstPage: true,
		lastPage: false,
		count: 0,
		currentItem: "",
		sortColumn: { path: "status", order: "asc" },
		translations: {
			"homeLabel": "Home",
			"titleLabel": "Customer",
			"addNewDataLabel": "Add New Customer",
		},
		totalElements: 0,
		commonValidationMessage: "",
	};


	loadData = async () => {
		let thisContext = this;
		thisContext.setState({ isLoading: true });
		await checkAuthentication().then(function(result) {
			if (result) {
				// console.log("current page: " + thisContext.state.currentPage);
				// console.log("page size: " + thisContext.state.pageSize);
				getAllPaginated(thisContext.state.currentPage, thisContext.state.pageSize,
					thisContext.state.fromDate, thisContext.state.toDate,
					thisContext.state.currentItem, thisContext.state.searchToken)
					.then((response) => {
						const { data: responseObj } = response;
						thisContext.setState({
							data: responseObj.content,
							pageSize: responseObj.pageable.pageSize,
							currentPage: responseObj.pageable.pageNumber,
							totalElements: responseObj.totalElements,
							firstPage: responseObj.first,
							lastPage: responseObj.last,
							isLoading: false
						});
						thisContext.getPagedData();
					})
					.catch((e) => {
						console.log(e);
					});
			} else {
				thisContext.props.history.push("/login");
			}
		});
	}

	async loadTranslations() {
		const thisContext = this;
		let translations = localStorage.getItem("translations");
		if (translations) {
			let translationsObj = JSON.parse(translations);
			thisContext.setState({
				translations: {
					"homeLabel": translationsObj["i18n.home.label"] ? translationsObj["i18n.home.label"] : thisContext.state.translations.homeLabel,
					"titleLabel": translationsObj["i18n.customer.titleLabel"]
						? translationsObj["i18n.customer.titleLabel"]
						: thisContext.state.translations.titleLabel,
					"addNewDataLabel": translationsObj["i18n.customer.addNewDataLabel"]
						? translationsObj["i18n.customer.addNewDataLabel"]
						: thisContext.state.translations.addNewDataLabel,
				},
			});
		} else {
			console.log("Alert!!!, Translations not found!");
		}
	}

	saveItem = async (item) => {
		await save(item);
	}

	deleteItem = async (item) => {
		let thisContext = this;
		const objData = this.state.data;
		try {
			await remove(item).then(function(response) {
				if (
					response.data.status == "FAILED"
				) {
					if (response.data.message != undefined) {
						thisContext.setState({
							commonValidationMessage: response.data.message,
						});
						thisContext.setState({ errorDismissed: false });
					}
				} else {
					const data = thisContext.state.data.filter((m) => m.id !== item.id);
					thisContext.setState({ data });
					thisContext.setState({
						commonValidationMessage: 'Successfully Deleted',
					});
					thisContext.setState({ successDismissed: false });
				}
			});
		} catch (error) {
			thisContext.setState({ data: objData });
		}
	}

	handleSearchTokenTextChange = ({ currentTarget: input }) => {
		this.setState({ searchToken: input.value });
	}

	htmlContent() {
		let thisContext = this;
		const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } = this.state;
		// const { totalCount, data } = this.getPagedData();
		const { totalElements, data } = this.state;
		const { fromDate, toDate, searchToken, categories, currentItem } = this.state;

		return (
			<React.Fragment>
				<Row>

					<Col md="12" xs="12" className="ml-2">
						<Card className="bg-light text-black mr-3">
							<Card.Title className="card-form-title-light">
								<Row>
									<Col md='3'>
										{this.state.translations.titleLabel}
									</Col>
									<Col className="text-right">
										{this.state.userPermissions.includes(this.state.DATA_CREATE) && (
											<button
												onClick={this.loadCreateForm}
												className="btn btn-sm btn-success"
											>
												<FontAwesomeIcon icon={faPlus} />
												&nbsp; {this.state.translations.addNewDataLabel}
											</button>
										)}
									</Col>
								</Row>
							</Card.Title>

							<Card.Body>
								<DataFilterControls
									fromDate={fromDate}
									toDate={toDate}
									searchToken={searchToken}
									categories={categories}
									currentItem={currentItem}
									onCategoryChange={this.handleCategoryChange}
									onSearchTokenChange={this.handleSearchTokenChange}
									onFromDateChange={this.handleFromDateChange}
									onToDateChange={this.handleToDateChange}
									onSearch={this.handleSearch}
								/>
								{/* <Card.Title>User Request</Card.Title> */}
								{/* form>(div.form-group>label+input.form-control)*2 */}
								<CustomersTable
									activeLanguage={this.props.stateValues.activeLanguage}
									columns={columns}
									sortColumn={sortColumn}
									onSort={this.handleSort}
									onLikeClick={this.handleChangeChk}
									data={data}
									onView={this.handleView}
									onEdit={this.handleEdit}
									onApprove={this.handleApprove}
									onDelete={this.handleDelete}
									onCreate={this.loadCreateForm}
								/>
								<p>Total {this.state.totalElements} records found!</p>
								{!this.state.errorDismissed && (
									<div style={{
										cursor: 'pointer',
										margin: '5px',
										backgroundColor: 'white',
										borderRadius: '.5rem'
									}}>
										<Alert variant="danger" onClose={() => { this.setState({ errorDismissed: true }) }} dismissible>
											<Alert.Heading>Ooh Failed to delete!</Alert.Heading>
											<p>
												{this.state.commonValidationMessage}
											</p>
										</Alert>
									</div>
								)}

								{!this.state.successDismissed && (
									<div style={{
										cursor: 'pointer',
										margin: '5px',
										backgroundColor: 'white',
										borderRadius: '.5rem'
									}}>
										<Alert variant="success" onClose={() => { this.setState({ successDismissed: true }) }} dismissible>
											<Alert.Heading>Success</Alert.Heading>
											<p>
												{this.state.commonValidationMessage}
											</p>
										</Alert>
									</div>
								)}
							</Card.Body>
							<Card.Footer>
								<Pagination
									currentPage={currentPage}
									itemsCount={totalElements}
									pageSize={pageSize}
									firstPage={firstPage}
									lastPage={lastPage}
									onPageChange={this.handlePageChange}
								/>
							</Card.Footer>
						</Card>
					</Col>
				</Row>
			</React.Fragment>
		);
	}
}

export default CustomerManager;
