import React from "react";
import Pagination from "../../common/pagination";
import { getAllPaginated, remove, save } from "./userRequestService";
import { checkAuthentication } from "../../common/authenticationService";
import { Col, Row, Card, Form, Button, Alert } from "react-bootstrap";
import UserRequestsTable from "./userRequestTable";
// import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faSearch } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../../common/UxCrudManager";
import DataFilterControls from "../../common/DataFilterControls";

class userRequestManager extends UxCrudManager {
  state = {
    isLoading: true,
    errorDismissed: true,
    successDismissed: true,
    commonValidationMessage: "",
    DATA_CREATE: "USER_REQUEST_CREATE",
    requiredPermission: "USER_REQUEST_READ",
    categories: [
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    moduleUrl: "/admin",
    basicUrl: "/admin/userRequests/",
    titleLabel: "User Request",
    currentPage: 0,
    data: [],
    fromDate: "",
    toDate: "",
    searchToken: "",
    translations: {
      titleLabel: "User Request",
      homeLabel: "Home",
      addNewDataLabel: "Add New User Request",
    },
    pageSize: 5,
    firstPage: true,
    lastPage: false,
    count: 0,
    totalElements: 0,
    currentItem: "",
    sortColumn: { path: "status", order: "asc" },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          homeLabel: translationsObj["i18n.common.homeLabel"]
            ? translationsObj["i18n.common.homeLabel"]
            : thisContext.state.translations.homeLabel,
          titleLabel: translationsObj["i18n.userRequest.titleLabel"]
            ? translationsObj["i18n.userRequest.titleLabel"]
            : thisContext.state.translations.titleLabel,
          addNewDataLabel: translationsObj["i18n.userRequest.addNewDataLabel"]
            ? translationsObj["i18n.userRequest.addNewDataLabel"]
            : thisContext.state.translations.addNewDataLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadData = async () => {
    const thisContext = this;
    await checkAuthentication().then(function (result) {
      if (result) {
        // console.log("current page: " + thisContext.state.currentPage);
        // console.log("page size: " + thisContext.state.pageSize);
        getAllPaginated(thisContext.state.currentPage, thisContext.state.pageSize,
          thisContext.state.fromDate, thisContext.state.toDate,
          thisContext.state.currentItem, thisContext.state.searchToken)
          .then((response) => {
            const { data: responseObj } = response;
            thisContext.setState({
              data: responseObj.content,
              pageSize: responseObj.pageable.pageSize,
              currentPage: responseObj.pageable.pageNumber,
              totalElements: responseObj.totalElements,
              firstPage: responseObj.first,
              lastPage: responseObj.last,
              isLoading: false,
            });
            // console.log(thisContext.state);
          })
          .catch((e) => {
            console.log("error occured!");
            console.log(e);
          });
      } else {
        thisContext.props.history.push("/login");
      }
    });
  };

  saveItem = async (item) => {
    await save(item);
  };

  deleteItem = async (item) => {
    await remove(item);
  };

  htmlContent() {
    let thisContext = this;
    const options = [
      { id: 0, value: "5" },
      { id: 1, value: "10" },
      { id: 2, value: "15" },
      { id: 3, value: "20" },
      { id: 4, value: "25" },
    ];
    const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } =
      this.state;

    const { totalCount, data } = this.getPagedData();
    const { fromDate, toDate, searchToken, categories, currentItem } = this.state;

    return (
      <React.Fragment>
        <Row>
          <Col md="12" xs="12" className="ml-2">
            <Card className="bg-light text-black mr-3 mb-3">
              <Card.Title className="card-form-title-light">
                <Row>
                  <Col md='3'>
                    {this.state.translations.titleLabel}
                  </Col>
                  <Col className="text-right">
                    {this.state.userPermissions.includes(this.state.DATA_CREATE) && (
                      <button
                        onClick={this.loadCreateForm}
                        className="btn btn-sm btn-success"
                      >
                        <FontAwesomeIcon icon={faPlus} />
                        &nbsp; {this.state.translations.addNewDataLabel}
                      </button>
                    )}
                  </Col>
                </Row>
              </Card.Title>

              <Card.Body>
                <Row>
                  <Col md='12'>
                    <DataFilterControls
                      fromDate={fromDate}
                      toDate={toDate}
                      searchToken={searchToken}
                      categories={categories}
                      currentItem={currentItem}
                      onCategoryChange={this.handleCategoryChange}
                      onSearchTokenChange={this.handleSearchTokenChange}
                      onFromDateChange={this.handleFromDateChange}
                      onToDateChange={this.handleToDateChange}
                      onSearch={this.handleSearch}
                    />
                  </Col>
                </Row>
                {/* <Card.Title>User Request</Card.Title> */}
                {/* form>(div.form-group>label+input.form-control)*2 */}
                <UserRequestsTable
                  activeLanguage={this.props.stateValues.activeLanguage} 
                  columns={columns}
                  sortColumn={sortColumn}
                  onSort={this.handleSort}
                  onLikeClick={this.handleChangeChk}
                  data={data}
                  onView={this.handleView}
                  onEdit={this.handleEdit}
                  onApprove={this.handleApprove}
                  onDelete={this.handleDelete}
                  onCreate={this.loadCreateForm}
                />

                <Col className="text-left" md='2'>
                  <div className="input-group mb-1">
                    <select
                    >
                      {options.map((option) => (
                        <option
                          key={option.id}
                          value={option.value}
                          onClick={() => this.handleSizeChange(option)}
                        >
                          {option.value}
                        </option>
                      ))}
                    </select>
                    <span className="label">entries per page</span>
                  </div>
                </Col>
                {!this.state.errorDismissed && (
                  <div style={{
                    cursor: 'pointer',
                    margin: '5px',
                    backgroundColor: 'white',
                    borderRadius: '.5rem'
                  }}>
                    <Alert variant="danger" onClose={() => { this.setState({ errorDismissed: true }) }} dismissible>
                      <Alert.Heading>Oops Failed to delete!</Alert.Heading>
                      <p>
                        {this.state.commonValidationMessage}
                      </p>
                    </Alert>
                  </div>
                )}

                {!this.state.successDismissed && (
                  <div style={{
                    cursor: 'pointer',
                    margin: '5px',
                    backgroundColor: 'white',
                    borderRadius: '.5rem'
                  }}>
                    <Alert variant="success" onClose={() => { this.setState({ successDismissed: true }) }} dismissible>
                      <Alert.Heading>Success</Alert.Heading>
                      <p>
                        {this.state.commonValidationMessage}
                      </p>
                    </Alert>
                  </div>
                )}
              </Card.Body>
              <Card.Footer>
                <Row>
                  <Col md='2'>
                    <p>Total {this.state.totalElements} records found!</p>
                  </Col>
                  <Col md='10'>
                    <Pagination
                      currentPage={currentPage}
                      itemsCount={totalCount}
                      pageSize={pageSize}
                      firstPage={firstPage}
                      lastPage={lastPage}
                      onPageChange={this.handlePageChange}
                    />
                  </Col>
                </Row>

              </Card.Footer>
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default userRequestManager;
